.sidebar {
    width: 10%;
    height: 100vh;
    min-height: 200px;
    overflow: auto;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: auto;
    margin-right: auto;
}

.item{
    margin: 2px;
}

.checkbox{
    margin: 5px;
}